.catalog--header {
  text-align: center;
  //border-bottom: 1px solid rgba(#556782, 0.3);
  padding-bottom: 40px;
  padding-top: 50px;
  h1 {
    margin: 0 0 50px;
  }
}

.catalog--tags {
  padding: 0 125px;
  ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    li {
      list-style: none;
      border: 1px solid rgba(#556782, 0.25);
      border-radius: 5px;
      white-space: nowrap;
      transition: 0.3s all;
      margin-bottom: 20px;
      margin-right: 12px;
      background: #fff;
      a {
        text-decoration: none;
        color: #000;
        font-size: 16px;
        padding: 10px 15px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &:hover {
        border-color: rgba(#0bbc93, 1);
        transition: 0.3s all;
      }
      &:active{
        background: rgba(#0bbc93,0.11);
        border-color: rgba(#0bbc93,0.11);
        color: #0bbc93;
      }
    }
  }
}
.stocks--wrapper{
  overflow: hidden;
}
.stocks {
  opacity: 1;
  transition: 0.5s all;
  display: flex;
  flex-direction: row;
  margin-top: 40px;
  .heading {
    white-space: nowrap;
    .text {
      font-size: 26px;
      font-family: $fmed;
    }
    .date {
      margin-top: 8px;
      font-size: 12px;
      color: #5d687a;
    }
  }
  &.updating{
    opacity: 0.3;
  }
  &.renew{
    //background: rgba(red,0.2);
  }
}

.stocks--items {
  margin-left: 30px;
  max-width: 100%;
  .swiper-wrapper {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .stock--item {
    list-style: none;
    font-size: 15px;
    white-space: nowrap;
    padding-right: 20px;
    span {
      padding: 7px 25px 7px 10px;
      background: #f2f4f5;
      display: inline-block;
      margin-left: 4px;
      border-radius: 3px;
      position: relative;
      &:after{
        content: "";
        display: block;
        position: absolute;
        right:9px;
        top:12px;
        width: 0;
        height: 0;
        border-style: solid;

      }
      &.stock-down{
        background-color: rgba(#f14941,0.1);
        &:after {
          border-width: 6px 4px 0 4px;
          border-color: #f14a41 transparent transparent transparent;
        }
      }
      &.stock-up{
        background-color: rgba(#00be95,0.1);
        &:after{
          border-width: 0 4px 6px 4px;
          border-color: transparent transparent #00be95 transparent;

        }
      }
    }
  }
  .swiper-slide{
    width: auto;
  }
}

.categories {
  margin-top: 35px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  a {
    display: block;
    width: 23.5%;
    max-width: 465px;
    //height: 495px;
    height: 350px;
    border-radius: 10px;
    padding: 25px;
    border-bottom: none;
    text-decoration: none;
    margin-bottom: 30px;
  }
  .heading {
    display: inline-block;
    font-size: 22px;
    line-height: 26px;
    font-family: $fmed;
    text-transform: uppercase;
    color: #fff;
    span{
      border-bottom: 1px solid #fff;
    }
  }
  &::after {
    content: "";
    flex: 0 0 23.5%;
    max-width: 465px;
  }
}

.category--item {
  overflow: hidden;
  &:before {
    transform: scale(1);
    transition: .2s all;
    width: 80% !important;
    height: 80% !important;
    background-position: left bottom !important;
  }
  &:hover {
    &:before {
      transform: scale(1.1);
      transition: .2s all;
    }
  }
}
.ci--tranzistory:before{
  width: 70% !important;
  height: 70% !important;
}

