.woocommerce nav.woocommerce-pagination ul{
  border: none;
  margin: 0;
}
.woocommerce nav.woocommerce-pagination ul li{
  border-right: none;
}
  .woocommerce nav.woocommerce-pagination ul li a,
  .woocommerce nav.woocommerce-pagination ul li span
  {
    border: 1px solid rgba(#5d687a,0.3);
    border-radius: 5px;
    background: #ffffff;
    padding: 20px;
    min-width: 57px;
    display: inline-block;
    margin: 0 4px;
    font-weight: bold;
    font-size: 17px;
    color: #0bbc93;
    &:hover{
      border: 1px solid rgba(#5d687a,1);
    }
  }
  .woocommerce nav.woocommerce-pagination ul li a:focus,
  .woocommerce nav.woocommerce-pagination ul li a:hover,
  .woocommerce nav.woocommerce-pagination ul li span.current
  {
    border: 1px solid transparent;
    border-radius: 5px;
    background: #e2eaed;
    padding: 20px;
    min-width: 57px;
    display: inline-block;
    margin: 0 4px;
    font-weight: bold;
    font-size: 17px;
  }

.alertwindow{
  position: fixed;
  right: 20px;
  top: 70px;
  padding: 15px 15px 15px 72px;
  width: 270px;
  background: #617791;
  color: #fff;
  border-radius: 6px;
  box-shadow: 0 1px 10px rgba(#000,0.2);
  opacity: 0;
  transition: all 0.3s;
  font-size: 15px;
  pointer-events: none;
  &.active{
    opacity: 1;
    transition: all 0.3s;
    pointer-events: auto;
  }
  .text{
    margin-bottom: 4px;
  }
  .textall{
    margin-bottom: 4px;
  }
  .btn-w{
    a{
      color: #fff;
    }
  }
  .ico-calc{
    display: block;
    width: 56px;
    height: 56px;
    top: 16px;
    right: auto;
    left: 0;
  }
  .btn-close{
    color: #fff;
    font-size: 20px;
    padding: 10px;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
  }
}

@media (min-width: 661px) and (max-width: 1024px){
  .page-template-catalog .footer .line2, .woocommerce-page .footer .line2{
    border-top: none;
    margin-top: 10px;
  }

  .catalog--header{
    padding-bottom: 25px;
    padding-top: 20px;
  }
  .catalog--header h1{
    margin-bottom: 30px;
  }
  .stocks{
    flex-direction: column;
    margin-top: 20px;
  }
  .stocks .heading{
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    margin-bottom: 10px;
    .date{
      margin-left: 15px;
    }
  }
  .stocks .heading .text{
    font-size: 17px;
  }
  .stocks--items{
    margin-left: 0;
    .swiper-slide{
      width: auto;
    }
  }
  .catalog--tags{
    padding: 0 10px;
  }
  .catalog--tags ul{
    //gap: 15px 10px;
  }
  .catalog--tags ul li a{
    padding: 7px 7px;
    font-size: 14px;
  }
  .categories{
    margin-top: 30px;
    //gap: 20px 0;
    justify-content: space-around;
    a:last-child:nth-child(odd){
      margin-right: auto;
    }
  }
  .categories a{
    width: 48%;
    height: 300px;
    padding: 10px;
    &:before{
      width: 100%;
      height: 100%;
      background-position: left bottom;
    }
  }
  .categories .heading{
    font-size: 17px;
    position: relative;
  }
  .catalog--products-ul{
    //gap: 30px 2%;
    flex-direction: row;
    justify-content: space-around;
  }
  .catalog--products-ul li{
    width: 48%;
    max-width: none;
    &:last-child:nth-child(odd){
      margin-right: auto;
    }
  }

}

@media (max-width: 660px){
  .categories{
    margin-top: 30px;
    //gap: 20px 4%;
    a{
      margin-bottom: 20px;
    }
  }
  .categories a{
